.navbar {
  position: relative;
  display: flex;

  align-items: center;
  padding: 2rem 1.5rem;

  @media (min-width: 767px) {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
}

.logo {
  width: 162px;
  margin-right: 2rem;
  cursor: pointer;

  path {
    fill: var(--colors-text);
  }

  &:hover {
    path {
      fill: var(--primary-300);
    }
  }
}

.burger {
  margin-left: auto;
  font-size: 1.75rem;
  color: var(--gray-300);
  cursor: pointer;

  &:hover {
    color: var(--gray-100);
  }
}

.mobileMenu {
  position: absolute;
  width: 100%;
  max-width: 320px;
  left: 50%;
  top: 100%;
  transform: translateX(-50%);
  overflow: hidden;

  background-color: var(--primary-600);
  border-radius: var(--radii);
  padding: 1.25rem;

  // & * {
  //   color: white !important;
  // }
}
